.plane__text {
  display: grid;
  align-items: center;
  position: relative;
  font-family: 'Catamaran', sans-serif;
  color: #000;
  font-weight: 400;
}
.plane__text span {
  font-size: 20px;
  font-weight: 400;
  line-height: 30px;
}
@media only screen and (max-width: 1600px) {
  .plane__text {
    font-size: 22px;
  }
}
