.progress__item-0,
.progress__item-3 {
  display: grid;
  width: 100%;
  grid-template-areas: "one" "two" "three" "four";
  grid-template-rows: 65px 200px;
  text-align: left;
}
.progress__item-0 header :nth-child(1) {
  grid-area: one;
  line-height: 65px;
}
.progress__item-0 header :nth-child(2) {
  grid-area: two;
  display: grid;
  justify-content: left;
}
.progress__item-0 :nth-child(3) {
  grid-area: three;
}
@media only screen and (max-width: 576px) {
  .progress__item-0,
  .progress__item-3 {
    grid-template-rows: 65px 100px;
  }
}
