/*
 *
 * Button
 *
*/
.button_theme_app,
.button_theme_buy,
.button_theme_island {
  box-shadow: 0 2px 10px #133072, 0 3px 6px rgba(0,0,0,0.1);
}
.button_theme_app,
.button_theme_buy {
  background-position: 70% 0;
  background-size: 150% 200%;
  transition: all 0.5s ease;
/* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#d5e6f7+0,d5adfb+100 */
  background: #d5e6f7 /* Old browsers */;
  background: -moz-linear-gradient(-45deg, #d5e6f7 0%, #d5adfb 100%) /* FF3.6-15 */;
  background: -webkit-linear-gradient(-45deg, #d5e6f7 0%, #d5adfb 100%) /* Chrome10-25,Safari5.1-6 */;
  background: linear-gradient(135deg, #d5e6f7 0%, #d5adfb 100%) /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */;
  transition: all 0.4s ease;
}
.button_theme_app:hover,
.button_theme_buy:hover {
  box-shadow: 0 0px 0px 0 rgba(0,40,120,0);
  background-position: 0 0;
}
.button_theme_app,
.button_theme_buy {
  border: 5px solid #050831;
}
.button__text {
  color: #f90052;
}
/*
 *
 * Button - End
