.grid__footer {
  display: grid;
  grid-template-areas: "left center right";
  grid-template-columns: 1fr 2fr 1fr;
  margin: 0 auto;
  text-align: center;
  width: 1250px;
}
.grid__footer .grid__block_l {
  grid-area: left;
}
.grid__footer .grid__block_c {
  grid-area: center;
}
.grid__footer .grid__block_r {
  grid-area: right;
}
@media only screen and (max-width: 1800px) {
  .grid__footer {
    max-width: 95%;
  }
}
@media only screen and (max-width: 1600px) {
  .grid__footer {
    max-width: 95%;
  }
}
@media only screen and (max-width: 1200px) {
  .grid__footer {
    max-width: 95%;
  }
}
@media only screen and (max-width: 768px) {
  .grid__footer {
    display: block;
  }
}
