.grid__template_1_4 {
  display: grid;
  width: 100%;
  grid-template-areas: "logo burger ul app";
  grid-template-rows: 120px;
  grid-template-columns: 2f auto 4fr 2fr;
  align-items: center;
}
.grid__template_1_4 .logo {
  grid-area: logo;
}
.grid__template_1_4 .burger {
  grid-area: burger;
}
.grid__template_1_4 ul {
  grid-area: ul;
}
.grid__template_1_4 .get {
  grid-area: app;
}
@media only screen and (max-width: 1800px) {
  .grid__template_1_4 {
    grid-template-columns: 0.5fr auto 4fr 2fr;
  }
}
@media only screen and (max-width: 1600px) {
  .grid__template_1_4 {
    grid-template-columns: 0.5fr auto 4fr 2fr;
  }
}
@media only screen and (max-width: 1440px) {
  .grid__template_1_4 {
    grid-template-columns: 0.5fr auto 3fr 1fr;
  }
}
@media only screen and (max-width: 1200px) {
  .grid__template_1_4 {
    grid-template-columns: 0fr 3fr auto 2fr;
  }
  .grid__template_1_4 .burger {
    grid-column: 3/3;
  }
  .grid__template_1_4 .header__menu {
    margin-right: 30px;
  }
}
@media only screen and (max-width: 768px) {
  .grid__template_1_4 {
    grid-template-columns: 0fr 1fr auto 3fr;
  }
}
@media only screen and (max-width: 425px) {
  .grid__template_1_4 {
    grid-template-columns: 0fr 0fr auto 3fr;
  }
}
