.grid__team {
  display: grid;
  width: 100%;
  grid-template-areas: "header header header" "subtitle subtitle subtitle" "about about about" "spacer spacer spacer";
  grid-template-rows: 100px 180px 1fr 100px;
  align-items: center;
  margin-top: 90px;
  justify-content: center;
}
.grid__team .header {
  grid-area: header;
}
.grid__team .subtitle {
  grid-area: subtitle;
}
.grid__team .about {
  grid-area: about;
}
.grid__team .spacer {
  grid-area: spacer;
}
@media only screen and (max-width: 1600px) {
  .grid__team {
    grid-template-columns: 0fr 0fr 0fr;
  }
}
@media only screen and (max-width: 1390px) {
  .grid__team {
    grid-template-columns: 1fr 0fr 0fr;
  }
}
@media only screen and (max-width: 425px) {
  .grid__team {
    grid-template-rows: 240px 140px 1fr 150px;
  }
}
