.progress__menu {
  display: grid;
  width: 100%;
  grid-template-areas: "left right";
  grid-template-rows: 120px;
  grid-template-columns: 150px 1fr;
  margin-bottom: 15px;
  padding: 0;
}
.progress__menu .progress__icon {
  grid-area: left;
  float: left;
  margin-right: 25px;
}
.progress__menu .progress__list {
  align-items: center;
}
@media only screen and (max-width: 425px) {
  .progress__menu {
    grid-template-columns: 100px 1fr;
  }
}
