.wrapper__prop {
  height: 100%;
  background: url("img/back__01.png");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
@media only screen and (max-width: 1100px) {
  .wrapper__prop {
    position: relative;
    z-index: 1;
    overflow: hidden;
    background: none;
  }
  .wrapper__prop:before {
    z-index: -1;
    position: absolute;
    left: 50%;
    top: 50%;
    content: url("img/iphone_02.png");
    opacity: 0.7;
    margin-right: -50%;
    transform: translate(-50%, -50%);
  }
}
@media only screen and (max-width: 768px) {
  .wrapper__prop:before {
    content: none;
  }
}
