.grid__progress-2 {
  display: grid;
  width: 100%;
  grid-template-areas: "l r" "spacer spacer";
  grid-template-rows: 1fr 180px;
  grid-template-columns: 50% 1fr;
}
.grid__progress-2 .grid__block_l {
  grid-area: l;
}
.grid__progress-2 .grid__block_r {
  grid-area: r;
}
.grid__progress-2 .spacer {
  grid-area: spacer;
}
.grid__progress-2 .item {
  grid-column: 3/2;
}
@media only screen and (max-width: 1800px) {
  .grid__progress-2 {
    grid-template-columns: 55% 2fr;
  }
}
@media only screen and (max-width: 1600px) {
  .grid__progress-2 {
    grid-template-columns: 65% 35%;
  }
  .grid__progress-2 .grid__block_l {
    padding-right: 4%;
  }
}
@media only screen and (max-width: 1390px) {
  .grid__progress-2 {
    grid-template-columns: 35% 65%;
  }
}
@media only screen and (max-width: 992px) {
  .grid__progress-2 {
    display: block;
    margin-bottom: 55px;
  }
}
