.grid__template_1_2 {
  display: grid;
  width: 100%;
  grid-template-columns: 1fr;
  grid-template-rows: 140px 130px;
}
@media only screen and (max-width: 768px) {
  .grid__template_1_2 .text {
    line-height: 130px;
  }
}
@media only screen and (max-width: 425px) {
  .grid__template_1_2 {
    grid-template-rows: 200px 70px;
  }
}
