.grid__template_sub {
  display: grid;
  width: 100%;
  grid-template-areas: "header header header" "subtitle subtitle subtitle" "main main main";
  grid-template-rows: 100px 180px 1fr 100px;
  align-items: center;
}
.grid__template_sub .header {
  grid-area: header;
}
.grid__template_sub .subtitle {
  grid-area: subtitle;
}
.grid__template_sub .main {
  grid-area: main;
}
@media only screen and (max-width: 1600px) {
  .grid__template_sub {
    grid-template-columns: 0fr 4fr 2fr;
  }
}
@media only screen and (max-width: 1390px) {
  .grid__template_sub {
    grid-template-columns: 0fr 4fr 2fr;
  }
}
@media only screen and (max-width: 425px) {
  .grid__template_sub {
    grid-template-rows: 240px 140px 1fr 150px;
  }
}
