.checkbox__box {
  position: relative;
  display: inline-block;
  border-radius: 3px;
  background: rgba(0,0,0,0.2);
  line-height: 14px;
  top: 2px;
  width: 14px;
  height: 14px;
  margin-right: 5px;
}
.checkbox__box:before {
  position: absolute;
  top: 1px;
  right: 1px;
  bottom: 1px;
  left: 1px;
  content: '';
  border-radius: 2px;
  background: #fff;
}
.checkbox__box:after {
  width: 15px;
  height: 15px;
  transform: translateY(-5px);
  position: absolute;
  top: -5px;
  left: 2px;
  content: '';
  background-image: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w322M13.5.5l-8%2012L1.7%208l-1%201.6L5.6%2015l9.1-13.4z%22%2F%3E%3C%2Fsvg%3E");
  background-size: 100%;
  visibility: hidden;
  opacity: 0;
  transition: transform 0.05s ease-out, opacity 0.05s ease-out, visibility 0s linear 0.05s;
}
