.grid__plane {
  display: grid;
  width: 100%;
  grid-template-columns: 1fr;
  grid-template-rows: 150px 900px;
}
.grid__plane .header {
  display: grid;
  align-items: end;
}
@media only screen and (max-width: 992px) {
  .grid__plane {
    grid-template-rows: 150px auto;
  }
  .grid__plane {
    margin-bottom: 50px;
  }
}
