.button_size_m {
  padding: 5px;
  width: 170px;
  font-size: 32px;
  line-height: 40px;
}
@media only screen and (max-width: 768px) {
  .button_size_m {
    width: 200px;
  }
}
@media only screen and (max-width: 425px) {
  .button_size_m {
    padding: 5px 15px;
    width: auto;
    font-size: 25px;
  }
}
