.progress__item-1,
.progress__item-2 {
  display: grid;
  align-items: center;
  position: relative;
}
.progress__item-1 img,
.progress__item-2 img {
  position: absolute;
}
.progress__item-1 img:nth-child(1),
.progress__item-2 img:nth-child(1) {
  position: absolute;
  left: 50%;
  margin-right: -50%;
  -webkit-transform: translate(-50%, 50%);
  -ms-transform: translate(-50%, 50%);
  transform: translate(-50%, 0);
  z-index: 9;
}
.progress__item-1 img:nth-child(2),
.progress__item-2 img:nth-child(2) {
  width: 770px;
}
@media only screen and (max-width: 992px) {
  .progress__item-2 img:nth-child(1) {
    display: none;
  }
}
