.tabs__title {
  display: grid;
  grid-template-areas: "one two";
  grid-template-columns: 2fr 1fr;
  align-items: center;
}
.tabs__title a {
  grid-area: one;
}
.tabs__title a,
.tabs__title>a:active {
  padding: 25px 0 25px 35px;
  font-size: 32px;
  font-weight: 700;
  display: block;
  font-family: 'Catamaran';
  color: #3a4e7a;
  text-transform: uppercase;
  letter-spacing: 1px;
  word-spacing: 3px;
  text-decoration: none;
}
