.base__image {
  position: relative;
}
.base__image img {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, 8%);
}
.base__image img:nth-child(1) {
  top: 40%;
  width: 600px;
}
.base__image img:nth-child(2) {
  top: 30%;
  width: 450px;
  transform: translate(-50%, 27%);
}
