.grid__progress {
  display: grid;
  width: 100%;
  grid-template-areas: "l r" "spacer spacer";
  grid-template-rows: 1fr 180px;
  grid-template-columns: 50% 1fr;
}
.grid__progress .grid__block_l {
  grid-area: l;
}
.grid__progress .grid__block_r {
  grid-area: r;
}
.grid__progress .spacer {
  grid-area: spacer;
}
.grid__progress .item {
  grid-column: 3/2;
}
@media only screen and (max-width: 1800px) {
  .grid__progress {
    grid-template-columns: 55% 2fr;
  }
}
@media only screen and (max-width: 1600px) {
  .grid__progress {
    grid-template-columns: 65% 35%;
  }
  .grid__progress .grid__block_l {
    padding-right: 4%;
  }
}
@media only screen and (max-width: 1390px) {
  .grid__progress {
    grid-template-columns: 60% 40%;
  }
}
@media only screen and (max-width: 768px) {
  .grid__progress {
    display: block;
  }
  .grid__progress .progress__item-1 {
    display: none;
  }
}
