.grid__template_service {
  display: grid;
  width: 100%;
  grid-template-areas: "l r";
  grid-template-rows: 780px;
  grid-template-columns: 40% 1fr;
}
.grid__template_service .grid__block_l {
  grid-area: l;
}
.grid__template_service .grid__block_r {
  grid-area: r;
}
.grid__template_service .spacer {
  grid-area: spacer;
}
.grid__template_service .item {
  grid-column: 3/2;
}
@media only screen and (max-width: 1800px) {
  .grid__template_service {
    grid-template-columns: 55% 2fr;
  }
}
@media only screen and (max-width: 1600px) {
  .grid__template_service {
    grid-template-columns: 55% 40%;
  }
}
@media only screen and (max-width: 1440px) {
  .grid__template_service {
    grid-template-columns: 55% 40%;
  }
}
@media only screen and (max-width: 1390px) {
  .grid__template_service {
    grid-template-columns: 100% 0;
  }
}
@media only screen and (max-width: 768px) {
  .grid__template_service {
    display: block;
  }
  .grid__template_service .grid__block_l {
    display: none;
  }
}
