.progress__list {
  display: grid;
  grid-template-areas: "one" "two";
  grid-template-rows: 60% 40%;
  color: #3a4e7a;
  font-size: 22px;
  font-weight: 300;
}
.progress__list div:nth-child(1) {
  grid-area: one;
}
.progress__list div:nth-child(2) {
  grid-area: two;
}
.progress__list .progress__menu_item:nth-child(1) {
  color: #050831;
  font-size: 19px;
  text-transform: capitalize;
  font-weight: 600;
}
