.wrapper__app {
  margin-top: 15%;
  height: 100%;
  background: url("img/iphone_04.png"), url("img/ipad_02.png");
  background-repeat: no-repeat;
  background-position: 10% 100%, 50% 50%;
}
@media only screen and (max-width: 1100px) {
  .wrapper__app {
    background: url("img/iphone_04.png");
    background-position: 100% 100%;
    background-repeat: no-repeat;
  }
}
