.grid__extra-02 {
  display: grid;
  width: 100%;
  grid-template-areas: "right left";
  grid-template-columns: 180px 1fr;
  grid-template-rows: 250px;
  padding: 20px 0;
  justify-content: center;
  align-items: center;
}
.grid__extra-02 .left {
  grid-area: left;
  padding-right: 15px;
}
.grid__extra-02 .right {
  grid-area: right;
}
@media only screen and (max-width: 1390px) {
  .grid__extra-02 {
    grid-template-columns: 100px 1fr;
  }
  .grid__extra-02 img {
    width: 100px;
  }
}
@media only screen and (max-width: 1100px) {
  .grid__extra-02 .header__s,
  .grid__extra-02 p {
    text-align: center;
  }
}
@media only screen and (max-width: 768px) {
  .grid__extra-02 .header__s {
    text-align: right;
  }
  .grid__extra-02 p {
    text-align: justify;
  }
}
@media only screen and (max-width: 576px) {
  .grid__extra-02 {
    display: block;
  }
}
