.grid__template_extra {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(3, 3fr);
  grid-template-rows: 15px 750px;
  grid-template-areas: ". prop ." "left prop right";
}
.grid__template_extra .spacer {
  grid-area: spacer;
  height: 150px;
}
.grid__template_extra .grid__block_l {
  grid-area: left;
}
.grid__template_extra .prop {
  grid-area: prop;
}
.grid__template_extra .grid__block_r {
  grid-area: right;
}
@media only screen and (max-width: 1100px) {
  .grid__template_extra {
    display: grid;
    width: 100%;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: 750px;
    grid-template-areas: "left right";
    justify-content: center;
  }
}
@media only screen and (max-width: 768px) {
  .grid__template_extra {
    display: block;
  }
  .grid__template_extra .spacer {
    display: none;
  }
}
