.burger {
  display: none;
}
@media only screen and (max-width: 1100px) {
  .burger {
    display: block;
    position: relative;
    width: 50px;
    height: 50px;
    cursor: pointer;
  }
}
.burger.menu-on .button_theme_burger::after {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  bottom: 0px;
}
.burger.menu-on .button_theme_burger::before {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  top: 0px;
}
.burger.menu-on .button_theme_burger {
  background: rgba(111,111,111,0);
}
.burger::after,
.burger::before,
.burger {
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
}
