.button_theme_app {
  font-family: 'Catamaran', sans-serif;
  position: relative;
  display: inline-block;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: #fff;
  border-radius: 80px;
  outline: 0;
  text-align: center;
  white-space: nowrap;
  -webkit-tap-highlight-color: transparent;
}
