.footer__links {
  text-align: left;
}
.footer__links h6 {
  font-size: 25px;
  margin-right: 15px;
  font-weight: 700;
  color: #3a4e7a;
}
.footer__links .stream__el {
  display: inline-block;
  list-style-type: none;
  margin-right: 20px;
}
@media only screen and (max-width: 992px) {
  .footer__links .stream__el {
    margin-right: 10px;
  }
}
@media only screen and (max-width: 768px) {
  .footer__links img {
    max-width: 20px;
  }
}
