.island {
  position: relative;
}
.island figcaption {
  font-family: 'Catamaran', sans-serif;
  font-size: 32px;
  font-weight: 700;
  line-height: 30px;
  color: #3a4e7a;
  margin: 25px 0;
}
.island span {
  position: absolute;
  bottom: 45px;
  display: block;
  width: 390px;
  margin: 0 auto;
  left: 0;
  text-align: left;
  font-size: 22px;
  font-weight: 500;
  line-height: 30px;
}
.island img {
  text-align: left;
  width: 120px;
}
.main .island:nth-child(1) {
  -webkit-animation-delay: 0;
  -moz-animation-delay: 0;
  -o-animation-delay: 0;
  animation-delay: 0;
}
.main .island:nth-child(2) {
  -webkit-animation-delay: 200ms;
  -moz-animation-delay: 200ms;
  -o-animation-delay: 200ms;
  animation-delay: 200ms;
}
.main .island:nth-child(3) {
  -webkit-animation-delay: 300ms;
  -moz-animation-delay: 300ms;
  -o-animation-delay: 300ms;
  animation-delay: 300ms;
}
@media only screen and (max-width: 1390px) {
  .island span {
    width: 300px;
  }
}
@media only screen and (max-width: 992px) {
  .island img {
    margin: 0 auto;
  }
  .island span {
    width: auto;
    text-align: center;
    position: relative;
  }
}
