.base__station {
  padding: 0;
  display: grid;
  width: 100%;
  grid-template-areas: "one two three";
  grid-template-columns: auto 1fr 2fr;
  align-items: center;
  height: 200px;
}
.base__station :nth-child(1) {
  grid-area: one;
}
.base__station :nth-child(2) {
  grid-area: two;
  text-align: left;
  padding-left: 20px;
}
.base__station :nth-child(3) {
  grid-area: three;
  text-align: left;
}
@media only screen and (max-width: 1390px) {
  .base__station {
    grid-template-columns: auto 1fr auto;
  }
}
@media only screen and (max-width: 992px) {
  .base__station {
    grid-template-columns: auto 1fr 3fr;
  }
}
@media only screen and (max-width: 768px) {
  .base__station {
    grid-template-columns: auto 1fr 2fr;
  }
}
@media only screen and (max-width: 425px) {
  .base__station :nth-child(3) {
    text-align: center;
  }
}
