.button_theme_burger {
  cursor: pointer;
  position: absolute;
  background: #6f6f6f;
  width: 100%;
  height: 10px;
  top: 50%;
  right: 0px;
  margin-top: -5px;
  opacity: 1;
}
.button_theme_burger:before {
  position: absolute;
  background: #6f6f6f;
  width: 50px;
  height: 10px;
  top: 20px;
  content: "";
  display: block;
}
.button_theme_burger:after {
  position: absolute;
  background: #6f6f6f;
  width: 50px;
  height: 10px;
  bottom: 20px;
  content: "";
  display: block;
}
