.grid__template_3_4 {
  display: grid;
  width: 100%;
  grid-template-areas: "header header header" "subtitle subtitle subtitle" "about about about" "spacer spacer spacer";
  grid-template-rows: 100px 180px 1fr 100px;
  align-items: center;
}
.grid__template_3_4 .header {
  grid-area: header;
}
.grid__template_3_4 .subtitle {
  grid-area: subtitle;
}
.grid__template_3_4 .about {
  grid-area: about;
}
.grid__template_3_4 .spacer {
  grid-area: spacer;
}
@media only screen and (max-width: 1600px) {
  .grid__template_3_4 {
    grid-template-columns: 0fr 4fr 2fr;
  }
}
@media only screen and (max-width: 1390px) {
  .grid__template_3_4 {
    grid-template-columns: 0fr 4fr 2fr;
  }
}
@media only screen and (max-width: 425px) {
  .grid__template_3_4 {
    grid-template-rows: 240px 140px 1fr 150px;
  }
}
