.grid__extra-01 {
  display: grid;
  width: 100%;
  grid-template-areas: "left right";
  grid-template-columns: 1fr 180px;
  grid-template-rows: 250px;
  padding: 20px 0;
  justify-content: center;
  align-items: center;
}
.grid__extra-01 .left {
  grid-area: left;
  padding-right: 15px;
}
.grid__extra-01 .right {
  grid-area: right;
}
@media only screen and (max-width: 1390px) {
  .grid__extra-01 {
    grid-template-columns: 1fr 110px;
  }
  .grid__extra-01 img {
    width: 100px;
  }
}
@media only screen and (max-width: 1100px) {
  .grid__extra-01 .header__s,
  .grid__extra-01 p {
    text-align: center;
  }
}
@media only screen and (max-width: 768px) {
  .grid__extra-01 .header__s {
    text-align: left;
  }
  .grid__extra-01 p {
    text-align: justify;
  }
}
@media only screen and (max-width: 576px) {
  .grid__extra-01 {
    display: block;
  }
}
