.header__m {
  font-size: 52px;
  font-family: 'Catamaran';
  font-weight: 600;
  width: 1300px;
  margin: 0 auto;
}
@media only screen and (max-width: 1440px) {
  .header__m {
    width: 85%;
  }
}
@media only screen and (max-width: 1390px) {
  .header__m {
    width: 85%;
    font-size: 52px;
  }
}
@media only screen and (max-width: 768px) {
  .header__m {
    font-size: 42px;
    line-height: 50px;
  }
}
