.main img {
  margin: 0 auto;
}
.main .plane__el {
  width: 500px;
  height: 530px;
}
@media only screen and (max-width: 1600px) {
  .main .stream__el,
  .main .plane__el {
    width: 410px;
    height: 550px;
  }
}
@media only screen and (max-width: 1390px) {
  .main .stream__el,
  .main .plane__el {
    width: 340px;
  }
}
@media only screen and (max-width: 1100px) {
  .main .stream__el,
  .main .plane__el {
    width: 300px;
  }
}
@media only screen and (max-width: 992px) {
  .main .stream__el {
    height: 450px;
  }
  .main .stream__el,
  .main .plane__el {
    width: 100%;
    grid-column: 1/1;
  }
  .main .plane__el {
    height: auto;
  }
}
